<template>
  <nav class="filterNav">
      <button @click="updateFilter('all')" :class="{ active: current === 'all'}">View All</button>
      <button @click="updateFilter('done')" :class="{ active: current === 'done'}">Done</button>
      <button @click="updateFilter('notDone')" :class="{ active: current === 'notDone'}">Not Done</button>
  </nav>
</template>

<script>
export default {
    props: ['current'],
    methods: {
        updateFilter(by) {
            this.$emit('filterChange', by)
        }
    }
}
</script>

<style>
.filterNav button {
    background: none;
    border: none;
    color: #bbb;
    outline: none;
    font-size: 12px;
    text-transform: uppercase;
    margin-right: 10px;
    letter-spacing: 1px;
    font-weight: bold;
    cursor: pointer;
  }
  .filterNav button.active {
    color: black;
  }
</style>